:root {
   --lightGrey: #d9d9d9;
   --grey: #9c9c9c;
	--adGrey: #393d42;
   --orange: #f48915;
   --darkGrey: #464d53;
   --caloryCard: #656565;
   --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
   --appColor: #3c3f45;
   --white: #fff;

   --duration-timing: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
   margin: 0;
}

ul,
ol {
   margin: 0;
   padding: 0;

   list-style: none;
}

button {
   padding: 0;
   font-family: inherit;
   cursor: pointer;
}

address {
   font-style: normal;
}

a {
   text-decoration: none;
   cursor: pointer;
   color: inherit;
}

img {
   display: block;
   max-width: 100%;
   height: auto;
}

picture {
   display: block;
}

.App {
   background-image: url(data:image/png;base64,);
   mix-blend-mode: overlay;
   background-color: var(--appColor);
   display: flex;
   flex-direction: column;
   gap: 6rem;
   overflow: hidden;
}
::-webkit-scrollbar {
   display: none;
}

.btn {
    background-color: #fff;
    border: 4px solid transparent;
    font-weight: 700;
    padding: 0.5rem;
    transition: all var(--duration-timing);
	 font-size: 13px;
}

.blur {
    background: rgba(253,120,43,.69);
    border-radius: 50%;
    -webkit-filter: blur(212px);
    filter: blur(212px);
    -webkit-filter: blur(150px);
    filter: blur(150px);
    position: absolute;
    z-index: -9;
}